import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  compare,
  currentDate,
  EditForm,
  EditViewProps,
  ERROR,
  formatDate,
  getSelectedItem,
  InputGroupInline,
  InputType,
  onlyAlphaNum,
  showToast,
  Tab,
  TabGroup,
  WindowComponent,
} from 'summer';
import useCepManager from '../../../../hooks/useCepManager';
import { CLASS_NAME, Responsavel } from '../../../../models/dtos/ger/responsavel';
import { RESPONSAVEL_TIPO_ENUM_VALUES } from '../../../../models/enumerated/ger/responsavelTipoEnum';
import { ESTADO_CIVIL_ENUM_VALUES } from '../../../../models/enumerated/mfp/estadoCivilEnum';
import { NACIONALIDADE_ENUM_VALUES } from '../../../../models/enumerated/mfp/nacionalidadeEnum';
import { validarCPFResponsavel } from '../../../../services/ger.service';
import CepBairroIS from '../../../components/ger/cepBairroIS/CepBairroIS';
import CepLocalidadeIS from '../../../components/ger/cepLocalidadeIS/CepLocalidadeIS';
import CepLogradouroIS from '../../../components/ger/cepLogradouro/CepLogradouroIS';
import CepTituloIS from '../../../components/ger/cepTituloIS/CepTituloIS';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

interface ResponsavelEditViewProps extends EditViewProps<Responsavel> {}

const ResponsavelEditView: FC<ResponsavelEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<Responsavel>({ defaultValues: selectedItem || {}, mode: 'onBlur' });

  const cepManager = useCepManager(formProps, {
    bairro: 'cepBairro',
    cep: true,
    localidade: 'cepLocalidade',
    nome: 'logradouro',
    titulo: 'cepTitulo',
    uf: 'cepUf',
  });

  useEffect(
    () => {
      if (selectedItem && selectedItem.id) {
        setTimeout(() => {
          // @ts-ignore
          formProps.setValue('cepFmt', { cep: selectedItem.cep, label: selectedItem.cep }, true);
        }, 800);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validarCPF = (): void => {
    if (formProps.getValues().cpf.length === 11) {
      validarCPFResponsavel(
        { cpf: formProps.getValues().cpf, id: formProps.getValues().id },
        {
          errorFunction: mensagem => {
            showToast(mensagem, ERROR);
          },
          thenFunction: (result: boolean) => {
            if (result) {
              const params: AlertProps = {
                message: 'CPF já cadastrado. Deseja Continuar?',
                type: AlertType.WARNING_YES_NO,
              };
              alertManager.emit(ACTION_EVENTS.SHOW, params);
            }
          },
        }
      );
    }
  };

  const validateExtras = (formData: Responsavel): string[] => {
    const erros = [];

    if (formData.dataNascimento) {
      if (compare(formData.dataNascimento.toString(), formatDate(currentDate())) > 0) {
        erros.push('Data Nascimento - O valor não pode ser maior do que a data atual.');
      }
    }

    if (!formData.cpf && !formData.cnpj) {
      erros.push('Campo C.P.F ou C.N.P.J deve ser preenchido.');
    }

    return erros;
  };

  // TODO Implementar a parte do eSocial
  return (
    <WindowComponent title="Manutenção de Responsável" wikiId={4398}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <TabGroup formProps={formProps}>
          <Tab title="Dados Pessoais">
            <Row>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps }}
                  id="cpf"
                  isPrependLabel={true}
                  label="CPF"
                  placeholder="Informe o CPF"
                  type={InputType.CPF}
                  onChange={validarCPF}
                />
              </Col>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps }}
                  id="dataNascimento"
                  isPrependLabel={true}
                  label="Data Nascimento"
                  placeholder="Informe a Data de Nascimento"
                  type={InputType.DATE}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps, validation: { maxLength: 250, required: true } }}
                  id="nome"
                  isPrependLabel={true}
                  label="Nome"
                  placeholder="Informe o Nome."
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps }}
                  id="nacionalidade"
                  isPrependLabel={true}
                  inputDropDownProps={{
                    optionsProvider: NACIONALIDADE_ENUM_VALUES,
                    selectProps: { isClearable: false },
                  }}
                  label="Nacionalidade"
                  placeholder="Informe a Nacionalidade"
                  tooltipProps={{ message: 'Nacionalidade do responsável.' }}
                  type={InputType.DROP_DOWN}
                />
              </Col>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps }}
                  id="estadoCivil"
                  isPrependLabel={true}
                  inputDropDownProps={{
                    optionsProvider: ESTADO_CIVIL_ENUM_VALUES,
                    selectProps: { isClearable: false },
                  }}
                  label="Estado Civil"
                  placeholder="Informe o Estado Civil"
                  type={InputType.DROP_DOWN}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps, validation: { ...onlyAlphaNum(0, 20) } }}
                  id="rg"
                  isPrependLabel={true}
                  label="RG"
                  placeholder="Informe o RG"
                />
              </Col>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps, validation: { maxLength: 60 } }}
                  id="orgaoEmissor"
                  isPrependLabel={true}
                  label="Órgão Emissor"
                  placeholder="Informe o Órgão Emissor"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps, validation: { required: true } }}
                  id="telefone"
                  isPrependLabel={true}
                  label="Telefone"
                  placeholder="Informe o Telefone"
                  type={InputType.TELEFONE}
                />
              </Col>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps, validation: { maxLength: 150 } }}
                  id="email"
                  isPrependLabel={true}
                  label="E-mail"
                  placeholder="Informe o E-mail"
                  type={InputType.EMAIL}
                />
              </Col>
            </Row>
          </Tab>
          <Tab title="Dados Responsável">
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="tipo"
              isPrependLabel={true}
              inputDropDownProps={{
                optionsProvider: RESPONSAVEL_TIPO_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              label="Tipo"
              placeholder="Informe o Tipo do Responsável"
              type={InputType.DROP_DOWN}
            />
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 30, required: true } }}
              id="cargo"
              isPrependLabel={true}
              label="Cargo"
              placeholder="Informe o Cargo"
            />

            <Row>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps, validation: { ...onlyAlphaNum(0, 15) } }}
                  id="numeroConselho"
                  isPrependLabel={true}
                  label="Nº no Conselho"
                  placeholder="Informe o Número no Conselho"
                />
              </Col>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps }}
                  id="dataValidadeCRC"
                  isPrependLabel={true}
                  label="Data Validade CRC"
                  placeholder="Informe o Data de Validade do CRC"
                  type={InputType.DATE}
                  tooltipProps={{ message: 'Data da Validade do CRC do Contador' }}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps }}
                  id="cnpj"
                  isPrependLabel={true}
                  label="CNPJ"
                  placeholder="Informe o CNPJ"
                  type={InputType.CNPJ}
                />
              </Col>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps, validation: { ...onlyAlphaNum(0, 20) } }}
                  id="cei"
                  isPrependLabel={true}
                  inputProps={{ style: { textTransform: 'uppercase' } }}
                  label="CEI"
                  placeholder="Informe o CEI"
                />
              </Col>
            </Row>
          </Tab>
          <Tab title="Endereço">
            <Row>
              <Col>
                <CepLogradouroIS
                  formProps={{ ...formProps, validation: { required: true } }}
                  id="cepFmt"
                  isPrependLabel={true}
                  label="CEP"
                  placeholder="Informe o CEP"
                  cepManager={cepManager}
                />
                <input type="hidden" name="cep" ref={formProps.register} />
              </Col>
              <Col>
                <CepTituloIS id="cepTitulo" isPrependLabel={true} formProps={{ ...formProps, validation: { required: true } }} />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps, validation: { maxLength: 150, required: true } }}
                  id="logradouro"
                  isPrependLabel={true}
                  label="Logradouro"
                  placeholder="Informe o Logradouro"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps, validation: { maxLength: 10, required: true } }}
                  id="numero"
                  isPrependLabel={true}
                  label="Número"
                  placeholder="Informe o Número"
                />
              </Col>
              <Col>
                <InputGroupInline
                  formProps={{ ...formProps, validation: { maxLength: 60 } }}
                  id="complemento"
                  isPrependLabel={true}
                  label="Complemento"
                  placeholder="Informe o Complemento"
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CepUFIS formProps={{ ...formProps, validation: { required: true } }} id="cepUf" />
              </Col>
              <Col>
                <CepLocalidadeIS formProps={{ ...formProps, validation: { required: true } }} id="cepLocalidade" />
              </Col>
            </Row>
            <Row>
              <Col>
                <CepBairroIS formProps={{ ...formProps, validation: { required: true } }} id="cepBairro" />
              </Col>
            </Row>
          </Tab>
        </TabGroup>
      </EditForm>
    </WindowComponent>
  );
};

export default ResponsavelEditView;
